<template>
  <module-page class="btb-pkg-table-basic">
    <module-page-head title="Table" :btnList="env.btnList" @clickBtn="openLink"/>
    <module-section>
      <!-- <template #content="slotProps"> -->
    <template #head>
      {{`Version: ${env.version}`}} <br/>
      {{`Release Date: ${env.updated}`}}
    </template>
      <p>
        {{'This module of table creator based on VueJS can do the help to make a table component to our page.'}}
      </p>
    </module-section>
    <module-section>
    <template #head>
      {{'INSTALLATION'}}
    </template>
      <module-pre>
<pre>
$ npm install --save @blacktoolbox/vue-table

<code class="pre_notice">// create a plugin for vue to use table</code>
import Vue from 'vue'

import BTBTable from '@blacktoolbox/vue-table'
import '@blacktoolbox/vue-table/lib/index.css'

Vue.use(BTBTable)
</pre>
      </module-pre>
    </module-section>
    <module-section>
    <template #head>
      {{'RENDER'}}
    </template>
      <module-pre>
<pre>
{{exampleRender}}
</pre>
      </module-pre>
    </module-section>
    <module-section>
    <template #head>
      {{'PARAMETERS'}}
    </template>
      <module-block>
      <template #title>
        {{'Basic'}}
      </template>
        <btb-vue-table class="page_table block_item" :headData="tableHeadArr" :bodyData="tableBodyArr_basic" xScrollable />
      </module-block>
      <module-block>
      <template #title>
        {{'headList'}}
      </template>
        <module-pre class="block_item">
<!-- <pre>
{{exampleDataList}}
</pre> -->
        </module-pre>
        <btb-vue-table class="page_table block_item" :headData="tableBodyArr_head" :bodyData="tableBodyArr_entry" xScrollable />
      </module-block>
      <module-block>
      <template #title>
        {{'dataList'}}
      </template>
        <module-pre class="block_item">
<!-- <pre>
{{exampleDataList}}
</pre> -->
        </module-pre>
        <btb-vue-table class="page_table block_item" :headData="tableBodyArr_body" :bodyData="tableBodyArr_entry" xScrollable />
      </module-block>
    </module-section>
    <module-section>
    <template #head>
      {{'NODE TREE'}}
    </template>
      <!-- <btb-vue-list class="page_node_tree" :dataList="nodeTree"/> -->
      <!-- <p>
        {{'Note: The layer count is counted base on 0.'}}
      </p> -->
    </module-section>
  </module-page>
</template>

<script>
import packageObj from '@/assets/definitions/packageObj'

const _exampleRender = `<btb-vue-table
        :headData=" Array " 
        :bodyData=" Array " 
        :bodyWidth=" String || Number"
        :xScrollable=" Boolean "/>`

const _exampleDataList = `entryObj = {
        id: '',
        title: '',
        defaultCollapsed: false,
        children: [...]
}, ...`

const _nodeTree = [
  {
    id: 'list',
    title: '<div> .btb-vue-list',
    children: [
      {
        id: 'layer',
        title: '<ul> .btb-vue-list-layer .layer-[layer count]',
        children: [
          {
            id: 'container',
            title: '<li> .layer-container .layer-[layer count]',
            children: [
              {
                id: 'entry',
                title: '<div> .container_entry .entry-[id] .entry-active',
                children: [
                  {
                    id: 'title',
                    title: '<div> .entry_title'
                  },
                  {
                    id: 'collapseBtn',
                    title: '<div> .entry_collapseBtn .collapseBtn-default',
                    children: [
                      {
                        id: 'arrow',
                        title: '<div> .collapseBtn_arrow'
                      }
                    ]
                  }
                ]
              },
              {
                id: 'sublayer',
                title: '<div>.list_sublayer',
                children: [
                  {
                    id: 'next',
                    title: '<ul>.btb-vue-list-layer.layer-[layer count + 1]'
                  }
                ]
              }
            ]
          }
        ]
      }
    ]
  }
]

export default {
  name: 'btb-pkg-list-basic',
  data () {
    return {
      env: {
        version: packageObj.table.version,
        updated: packageObj.table.updated,
        btnList: [
          { id: 'github', fa: ['fab', 'github'], url: 'https://github.com/BlackToolBoxLaboratory/vue-table' },
          { id: 'npm', fa: ['fab', 'npm'], url: 'https://www.npmjs.com/package/@blacktoolbox/vue-table' }
        ]
      },
      exampleRender: _exampleRender,
      exampleDataList: _exampleDataList,
      tableHeadArr: [
        { name: 'Property Name', index: 'title' },
        { name: 'Type', index: 'type' },
        { name: 'Default', index: 'default' },
        { name: 'Notice', index: 'notice' }
      ],
      tableBodyArr_basic: [
        { title: 'headData', type: 'Array', default: '[]', notice: 'List of headObj for table.' },
        { title: 'bodyData', type: 'Array', default: '[]', notice: 'List of dataObj for table' },
        { title: 'bodyWidth', type: 'String || Number', default: 'undefined', notice: 'Width of table.' },
        { title: 'xScrollable', type: 'Boolean', default: 'undefined', notice: 'Active\'s table x scroll bar..' }
      ],
      tableBodyArr_head: [
        { title: 'id', type: 'String', default: 'undefined', notice: 'Identity of entry' },
        { title: 'title', type: 'String', default: '\'\'', notice: 'Show name of entry.' },
        { title: 'defaultCollapsed', type: 'String', default: 'undefined', notice: 'Default value to collapsed of extend.' },
        { title: 'children', type: 'Array', default: '[]', notice: 'sublist' }
      ],
      tableBodyArr_body: [
        { title: 'id', type: 'String', default: 'undefined', notice: 'Identity of entry' },
        { title: 'title', type: 'String', default: '\'\'', notice: 'Show name of entry.' },
        { title: 'defaultCollapsed', type: 'String', default: 'undefined', notice: 'Default value to collapsed of extend.' },
        { title: 'children', type: 'Array', default: '[]', notice: 'sublist' }
      ],
      nodeTree: _nodeTree
    }
  }
}
</script>

<style lang="scss">

</style>
